import "./hero.css";
import TypedTitles from "./TypedTitles";
const Hero = (props) => {
  return (
    <div className="hero-con">
      <div className="hero-inner">
        <h1 style={{ display: props.homePage ? "none" : "block" }}>
          {props.title}
        </h1>
        <h1
          className="primary-colo mt-4"
          style={{ display: props.homePage ? "block" : "none" }}
        >
          <TypedTitles />
        </h1>
        <p style={{ display: props.homePage ? "none" : "block" }}>
          {props.info}
        </p>
        {/* {props.homePage?} */}

        <div className="join-now-btn-con">
          <a
            style={{ display: props.homePage ? "block" : "none" }}
            target="_blank"
            href="https://www.google.com/forms/about/"
          >
            <button className="join-now-btn">Join Now</button>
          </a>
        </div>
      </div>
    </div>
  );
};
export default Hero;
