import React, { Component } from "react";
// import { render } from 'react-dom';
import Typed from "react-typed";

class TypedTitles extends Component {
  render() {
    return (
      <div>
        <Typed
          strings={[
            "Wai Volleyball Club ",
            `Volleyball is <i class="fas fa-heart primary-color"></i>&nbsp;`,
          ]}
          showCursor={false}
          typeSpeed={40}
          backSpeed={50}
          loop={true}
        />
        <br />
      </div>
    );
  }
}
export default TypedTitles;
