import React, { Component } from "react";
import Slider from "react-slick";

import "./myslider-2.css";

// Import css files
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const Slider1Images = [
  {
    url: "https://images.unsplash.com/photo-1484482340112-e1e2682b4856?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxleHBsb3JlLWZlZWR8MTR8fHxlbnwwfHx8fA%3D%3D&w=1000&q=80",
  },
  { url: "https://wallpapercave.com/wp/cAO5uXj.jpg" },
  { url: "https://wallpaper.dog/large/248128.jpg" },
  { url: "https://www.marshallsindia.com/smallItemImages/ZA%20601.jpg" },
  {
    url: "https://image.freepik.com/free-vector/blue-sports-background-speed-effect-style_1017-20697.jpg",
  },
];

export default class MySlider2 extends Component {
  render() {
    const settings = {
      dots: false,
      infinite: true,
      speed: 500,
      slidesToShow: 3,
      slidesToScroll: 3,
    };
    return (
      <div>
        <h2 className="container">Teams Involved</h2>
        <Slider {...settings}>
          {Slider1Images.map((slide) => (
            <div key={slide.url} className="slide-2">
              <img src={slide.url} alt="none" />
            </div>
          ))}
        </Slider>
      </div>
    );
  }
}
