import Header from "../components/Header";
import Hero from "../components/Hero";
import MySlider1 from "../components/MySlider1";
import MySlider2 from "../components/MySlider2";
import "./tournaments.css";
const Tournaments = (props) => {
  return (
    <div>
      <div
        className="top-con"
        style={{ backgroundImage: `url(${props.bgImage})` }}
      >
        <Header />
        <Hero title="Tournaments" info="Many more" />
      </div>
      {/* <section> */}
      <div className="section-inner container">
        <div className="tournaments-2021">
          <div className="title">Tournament 2021</div>
        </div>
      </div>
      {/* </section> */}

      <section>
        <div className="section-inner container">
          {/* <h2 className="container">Details</h2> */}
          {/* <GoogleMap /> */}
          <MySlider1 />
        </div>
      </section>
      <section>
        <div className="section-inner container">
          {/* <h2 className="container">Details</h2> */}
          {/* <GoogleMap /> */}
          <MySlider2 />
        </div>
      </section>
    </div>
  );
};
export default Tournaments;
