import React, { useState } from "react";
import { Link } from "react-router-dom";

import "./header.css";
const Header = () => {
  const [openMenu, setMenuOpen] = useState(false);
  function toggleMenu() {
    setMenuOpen(true);
  }
  function closeMenu() {
    setMenuOpen(false);
  }

  const pathName = window.location.hash.substring(1);
  const currentPath = console.log(pathName);

  return (
    <div className="header-con">
      <div className="container">
        <header>
          <Link to="/">
            <div className="logo">
              <i class="fas fa-volleyball-ball"></i>WVC
            </div>
          </Link>
          <ul className="menu">
            <Link
              //  className="link link-active"
              className={pathName === "/" ? "d-menu d-menu-active" : "d-menu "}
              to="/"
            >
              <li>Home</li>
            </Link>
            <Link
              className={
                pathName === "/tournaments"
                  ? "d-menu  d-menu-active"
                  : "d-menu "
              }
              to="/tournaments"
            >
              <li>Tournaments</li>
            </Link>
            <Link
              className={
                pathName === "/players" ? "d-menu  d-menu-active" : "d-menu "
              }
              to="/players"
            >
              <li>Players</li>
            </Link>
            <Link
              className={
                pathName === "/contact" ? "d-menu  d-menu-active" : "d-menu "
              }
              to="/contact"
            >
              <li>Contact</li>
            </Link>
          </ul>

          {/* <i className="fas fa-bars phn-menu-btn" onClick={toggleMenu} /> */}
          <div className="phn-menu-btn hover" onClick={toggleMenu}>
            <i class="fas fa-bars"></i>
          </div>
          <div
            style={{ width: openMenu ? "300px" : "0px" }}
            className="phn-menu-con"
          >
            <div className="phn-menu">
              <li className="close-menu-btn" onClick={closeMenu}>
                <i class="fas fa-times"></i>
              </li>

              <ul>
                <Link className="link link-active" to="/">
                  <li
                    className={
                      // pathName === "/wvc"
                      //  ||
                      pathName === "/" ? "active-menu" : ""
                    }
                  >
                    <i class="fas fa-house-damage"></i> Home
                  </li>
                </Link>
                <Link className="link" to="/tournaments">
                  <li
                    className={pathName === "/tournaments" ? "active-menu" : ""}
                  >
                    <i class="fas fa-volleyball-ball"></i> Tournaments
                  </li>
                </Link>
                <Link className="link" to="/players">
                  <li className={pathName === "/players" ? "active-menu" : ""}>
                    <i class="fas fa-user-friends"></i> Players
                  </li>
                </Link>
                <Link className="link" to="/contact">
                  <li className={pathName === "/contact" ? "active-menu" : ""}>
                    <i class="fas fa-id-card-alt"></i> Contact
                  </li>
                </Link>
              </ul>
            </div>
          </div>
        </header>
      </div>
    </div>
  );
};
export default Header;
