import About from "../components/About";
import GoogleMap from "../components/GoogleMap";
import Header from "../components/Header";
import Hero from "../components/Hero";
import MySlider1 from "../components/MySlider1";
import MySlider2 from "../components/MySlider2";
import "./home.css";
const Home = (props) => {
  return (
    <div>
      <div
        className="top-con"
        style={{ backgroundImage: `url(${props.bgImage})` }}
      >
        <Header />
        <Hero
          bgImage={props.bgImage}
          homePage={true}
          title="Wai Volleyball Club"
          info="Many more"
        />
      </div>
      {/* <p>Home</p> */}
      <div className="section-inner container">
        <div className="home-op">
          <div className="title">Wai VolleyBall Club</div>
        </div>
      </div>
      {/* <section>
        <div className="took-up">Wai Volleyball Club</div>
      </section> */}
      <section></section>
      <section></section>
      <section></section>
      <section>
        <div className="container">
          <MySlider1 />
        </div>
      </section>
      <section>
        <div className="container">
          <MySlider2 />
        </div>
      </section>
      <section>
        <div className="section-inner container">
          <h2 className="container">Find on Map</h2>
          <GoogleMap />
        </div>
      </section>
      <section>
        <div className="section-inner container">
          <h2 className="container">About Coach</h2>
          <About />
        </div>
      </section>
    </div>
  );
};
export default Home;
